import {
    Input,
    Modal,
    Button,
    Form,
    message,
    Result,
    Select,
    Typography,
    Upload
} from 'antd';
import type {UploadProps} from 'antd';
import {InboxOutlined} from '@ant-design/icons';
import React, {useEffect, useState} from 'react';
import {useNavigate} from 'react-router-dom';
import './index.scss';

const {Title} = Typography;

const {Dragger} = Upload;

const props: UploadProps = {
    name: 'file',
    multiple: true,
    // action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
    onChange(info) {
        const {status} = info.file;
        if (status !== 'uploading') {
            console.log(info.file, info.fileList);
        }
        if (status === 'done') {
            message.success(`${info.file.name} 文件上传成功.`);
        } else if (status === 'error') {
            message.error(`${info.file.name} 文件上传失败.`);
        }
    },
    onDrop(e) {
        console.log('Dropped files', e.dataTransfer.files);
    },
    progress: {
        strokeColor: {
            '0%': '#108ee9',
            '100%': '#87d068',
        },
        strokeWidth: 3,
        format: percent => percent && `${parseFloat(percent.toFixed(2))}%`,
    },
};

const TravelForm = () => {
    const navigate = useNavigate();
    const [confirmLoading, setConfirmLoading] = useState(false);
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [isRenderResult, setIsRenderResult] = useState(false);
    const [departmentOptions, setDepartmentOptions] = useState([]);
    const [RenderResultTitle, setRenderResultTitle] = useState('正在获取部门变更申请记录');
    const [form] = Form.useForm();
    const departmentUid = Form.useWatch('departmentUid', form);
    const changeReason = Form.useWatch('reason', form);
    const changeFile = Form.useWatch('file', form);

    useEffect(() => {
        getDepartmentOptions();
    }, []);

    // 获取部门列表
    const getDepartmentOptions = () => {

    }

    const RenderModal: React.FC = () => {
        return (
            <Modal
                title="Title"
                open={isModalVisible}
                onOk={handleOk}
                confirmLoading={confirmLoading}
                onCancel={handleCancel}
            >
                <p>变更部门：{departmentUid}</p>
                <p>变更原因：{changeReason}</p>
                {/*将变更材料 changeFile 中的 fileList 数组中的 每一项 name 输出出来*/}
                <p>变更材料：{changeFile ? changeFile.fileList.map((item: any) => item.name).join('、') : '您暂未选择任何变更材料'}</p>
            </Modal>
        )
    }

    const handleCancel = () => {
        console.log('Clicked cancel button');
        setIsModalVisible(false);
    };

    const handleOk = () => {
        setConfirmLoading(true);
        setTimeout(() => {
            setIsModalVisible(false);
            setConfirmLoading(false);
        }, 2000);
    };

    const onFinish = (values: any) => {
        setIsModalVisible(true)
    };

    const onReset = () => {
        form.resetFields();
    };

    return isRenderResult ? (
        <Result
            status="warning"
            title={RenderResultTitle}
            extra={
                <Button type="primary" key="console" onClick={() => {
                    navigate('/record/leaveList', {replace: true})
                }}>
                    查看差旅报销申请记录
                </Button>
            }
        />) : (
        <div className={'body'}>
            <RenderModal/>
            <div className="zent-alert-example">
                <Title level={2} className={'tit'}>差旅报销申请</Title>
                <Form
                    form={form}
                    name="basic"
                    labelCol={{span: 8}}
                    wrapperCol={{span: 16}}
                    initialValues={{remember: true}}
                    onFinish={onFinish}
                    autoComplete="off"
                >
                    <Form.Item
                        label="出差目的地"
                        name="departmentUid"
                        rules={[{required: true, message: '请输入出差目的地'}]}
                    >
                        <Input showCount maxLength={20}/>
                    </Form.Item>

                    <Form.Item
                        label="出差费用"
                        name="departmentUid"
                        rules={[{required: true, message: '请输入出差费用'}]}
                    >
                        <Input prefix="￥" suffix="RMB"/>
                    </Form.Item>

                    <Form.Item
                        label="出差原因"
                        name="reason"
                        rules={[{required: true, message: '请输入出差原因'}]}
                    >
                        <Input.TextArea rows={4} placeholder={"请输入出差原因"}/>
                    </Form.Item>

                    <Form.Item
                        label="报销单据"
                        name="file"
                        rules={[{required: true, message: '请选择报销单据'}]}
                    >
                        <Dragger {...props}>
                            <p className="ant-upload-drag-icon">
                                <InboxOutlined/>
                            </p>
                            <p className="ant-upload-text">单击或拖动文件到此区域进行上传</p>
                            <p className="ant-upload-hint">
                                支持单个或批量上传
                            </p>
                        </Dragger>
                    </Form.Item>

                    <Form.Item wrapperCol={{offset: 8, span: 16}}>
                        <Button type="primary" htmlType="submit">
                            提交
                        </Button>
                        <Button htmlType="button" onClick={onReset} style={{marginLeft: 8}}>
                            重置
                        </Button>
                    </Form.Item>
                </Form>
            </div>
        </div>
    )
        ;
};

const Index = () => {
    return (
        <TravelForm/>
    )
}

export default Index;